/* Page Wrapper + Menu */
#page-wrapper {
		-webkit-transition: opacity 0.5s ease;
		transition: opacity 0.5s ease;
		opacity: 1;
		padding-top: 3em;
}
#page-wrapper:before {
			background: transparent;
			content: '';
